___CSS_LOADER_EXPORT___.push([module.id, "\n:root {\n  /* colors */\n  --fg: #333;\n  --bg: #fff;\n  --accentBg: #fafafa;\n  --distantFg: #fff;\n  --border: #888;\n  --primary: #edad0b;\n  --primaryHover: #f2c145;\n  --primaryActive: #d59b0a;\n  --primaryShadow: 0 0 0 2px rgba(237, 173, 11, 0.2);\n  --danger: #c7243a;\n  --dangerHover: #d04255;\n  --dangerActive: #b32034;\n  --dangerShadow: 0 0 0 2px rgba(199, 36, 58, 0.2);\n\n  /* typography */\n  --fontSizeTitle: 28px;\n  --fontSizeXLarge: 18px;\n  --fontSizeLarge: 16px;\n  --fontSizeMedium: 14px;\n  --fontSizeSmallIcon: 16px;\n  --fontSizeIcon: 26px;\n  --fontSizePart: 48px;\n  --multilineTextLineHeight: 1.5;\n\n  /* layout */\n  --borderRadiusSmall: 1px;\n  --borderRadius: 2px;\n  --paddingXLarge: 20px 22px;\n  --paddingLarge: 14px 16px;\n  --paddingMedium: 10px 12px;\n  --paddingSmall: 8px 10px;\n  --paddingMinimal: 4px;\n  --marginXLarge: 20px;\n  --marginLarge: 16px;\n  --marginMedium: 12px;\n  --marginSmall: 8px;\n  --marginXSmall: 4px;\n\n  /* other */\n  --textareaLineHeight: 1.4;\n  --selectPadding: 10px calc(12px + 1em) 10px 12px;\n  --numberPadding: 10px calc(12px + 2em) 10px 12px;\n  --inputAddonTop: 10px; /* paddingMedium[0] */\n  --sliderRailHeight: 0.375em;\n  --sliderMarkHeight: 0.75em;\n  --sliderKnobSize: 1.25em;\n  --sliderValueMargin: 0.125em;\n  --sliderValueWidth: 2.5em;\n  --colorSliderRailHeight: 1.125em;\n  --fieldsetBottomSpacing: 24px;\n  --mediaIconSize: 34px;\n  --popoverShadow: rgb(0 0 0 / 19%) 0 10px 20px, rgb(0 0 0 / 23%) 0 6px 6px;\n  --tabButtonPadding: 0 4px 8px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/App.vue"],"names":[],"mappings":";AAiKA;EACE,WAAW;EACX,UAAU;EACV,UAAU;EACV,mBAAmB;EACnB,iBAAiB;EACjB,cAAc;EACd,kBAAkB;EAClB,uBAAuB;EACvB,wBAAwB;EACxB,kDAAkD;EAClD,iBAAiB;EACjB,sBAAsB;EACtB,uBAAuB;EACvB,gDAAgD;;EAEhD,eAAe;EACf,qBAAqB;EACrB,sBAAsB;EACtB,qBAAqB;EACrB,sBAAsB;EACtB,yBAAyB;EACzB,oBAAoB;EACpB,oBAAoB;EACpB,8BAA8B;;EAE9B,WAAW;EACX,wBAAwB;EACxB,mBAAmB;E